/*------------------------------------imagen circular----------------------------------*/
.image-cropper {
    position: relative;
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 50%;
}
.image-cropper img {
    width: 100%;
  height: auto;
}
/*------------------------------------cambiar la letra----------------------------------*/
.font {
    font-family: 'PT Mono', monospace;
}
.animated {
  text-indent: 8px;
  
}
/*-----------------------------------animacion de la frase------------------------*/
.animated:before {

    content: " pianist";
  animation : topToBottom 5s infinite 0s;
}

@keyframes topToBottom {
  0% {
    content: " Competitive Programming";
    color:purple;
  }
  25% {
    content: " Music";
    color:pink;
  }
  50% {
    content: " Coding";
    color:blue;
  }
  100% {
    content: " Competitive Programming";
  color:purple;
  }
}
/*---------------------------------------animacion del jumbotron------------------------*/
    
@keyframes ond {
    0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
/*----------------------------------------tamaño de la imagen del git-----------------------*/
.img {
    width : 50px;
    height : 50px;
}

/*----------------------------------------texto bonito-------------------------*/
.cute-text{
